@import '../../index.scss';

.formW {
    margin: 5% auto;
    text-align: center;

    h2 {
        margin: auto; 
        text-align: center;
        font-weight: 150;
        letter-spacing: .18rem;
        font-size: 4em;
        margin-bottom: 1%;

        @include respond(tab-port) {
            font-size: 37px;
        }

        @include respond(phone){
            font-size: 32px;
            width: 86%;
            margin-bottom: 3%;
        }
    }

    form {
        width: 90%;
        margin: 0 auto;
        font-size: 22px;
        
        @include respond(tab-port) {
            font-size: 16px;
           // width: 90%;
        }


        p {
            line-height: 40px;
            margin-bottom: 30px;

            @include respond(tab-port) {
                line-height: 40px;
                margin-bottom: 6%;
            }
            
            .flexL {
                margin-right: 20px;

                input {
                    width: auto;
                }

            }

            label {
/*                 margin-right: 10px;
                width: 410px;
                font-size: large;
                display: inline-block;
                text-align: start; */
                display: block;
                width: auto;
                float: left;

    /*                 @include respond(tab-port) {
                        width: 100%;
                    }

                    @include respond(phone){
                        font-size: 14px;
                    } */
            }

            textarea {
                width: 100%;
                
                &:focus {
                    outline-color: $idylle-color;
                    box-shadow: -2px -2px 4px $idylle-color;
                    transition: .1s;
                    transition-property: box-shadow; 
                }

                @include respond(tab-port){
                    width: 100%;
                }
            }

            input {
                padding: 10px 5px;
                border: 2px solid white;
                border-radius: 5px;
                width: 100%;
                color: black;

                &:focus {
                    outline-color: $idylle-color;
                    box-shadow: -2px -2px 4px $idylle-color;
                    transition: .1s;
                    transition-property: box-shadow;
                }

                @include respond(tab-port) {
                    width: 100%;
                }

                &[type="date"] {
                    width: 100%;

                    @include respond(tab-port) {
                        width: 100%;
                    }
                }
            }
        }

         /*------------Bouton---------------*/
         button {
            font-family: inherit;
            font-size: 20px;
            background: $idylle-color;
            color: white;
            padding: 0.5em .8em;
            padding-left: 0.9em;
            display: flex;
            align-items: center;
            border: none;
            border-radius: 16px;
            overflow: hidden;
            transition: all 0.2s;
            margin: 0 auto;

            @include respond(phone){
                padding: .2em .5em;
                font-size: 18px;
            }
          }
          
          button span {
            display: block;
            margin-left: 0.3em;
            transition: all 0.3s ease-in-out;
          }
          
          button svg {
            display: block;
            transform-origin: center center;
            transition: transform 0.3s ease-in-out;
          }
          
          button:hover .svg-wrapper {
            animation: fly-1 0.6s ease-in-out infinite alternate;
          }
          
          button:hover svg {
            transform: translateX(1.9em) rotate(45deg) scale(1.1);
          }
          
          button:hover span {
            transform: translateX(5em);
          }
          
          button:active {
            transform: scale(0.95);
          }
          
          @keyframes fly-1 {
            from {
              transform: translateY(0.1em);
            }
          
            to {
              transform: translateY(-0.1em);
            }
          }
          
        /*-----------------------------------------------------*/


        .radioRow {
            overflow: hidden;
            margin-bottom: 5%;

            .left {
                float: left;
                margin-left: 10%;
               /* margin-right: 26.5%; */

/*                 @include respond(phone){
                    width: 100%;
                } */

                label {
                    font-size: 18px;
                    margin-right: 10px;
                    width: auto;

                    @include respond(phone){
                        font-size: 14px;
                        width: 100%;
                    }
                }
            }

            .right {
                float: left;

                @include respond(tab-port) {
                    float: left;
                    margin-left: 4%;
                    margin-top: 3%;
                }

                label {
                    margin: 0 20px;
                    width: auto;
                    font-size: 18px;

                    @include respond(phone){
                        font-size: 14px;
                    }
                }
            }

            input[type="radio"] {
                margin-right: 10px;
            }

        }

        .textStart {
            label {
                width: auto;
                text-align: justify;
                line-height: 30px;
                margin-bottom: 1%;
            }
        }
    }
}